@media only screen and (max-width:375px){

body{
  width: 100%;
  height: auto;
}

  div.edit-name {
    font-size: 40px;
    color:rgb(79, 58, 158);
    margin-top: 30px;
    clear: left;
    text-align: center;
  }

  div.edit-position {
    font-size: 24px;
    text-align: center;
    color: grey;
    margin-top: 24px;
    word-break: break-word;
  }
  div.photo-Container img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    border-radius: 38px;
    align-content: center;
    height: auto;
    width: 280px;
  }

  div.ui .cards{
    margin-left: 10px;
  }

  div.card-deck{
    width: 94%;
    border: 2px solid gray;
    border-radius: 15px;
  }

  div.card{
    width:100%;
  }
  div.about-Photo-Edit img{
    display: none;
  }

  div.about-me-details{
    margin-top:25px;
    width: 100%;
  }
  
  div.about-me-details p{
    width: 100%;
    word-break: break-word;
    font-size: 18px;
  }

}








.edit-name {
  font-size: 70px;
  color:rgb(79, 58, 158);
  margin-top: 50px;
  text-align: center;
}

.photo-Container img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  border-radius: 38px;
  align-content: center;
  height: auto;
  width: 340px;
}

.about-Photo-Edit{
  float: left;
}

.about-Photo-Edit img{
  width: 50%;
  transform: rotate(100deg);
  margin-top: 100px;
  border-radius: 20px;
  float: left;
}
.edit-position {
  font-size: 27px;
  text-align: center;
  color: grey;
  margin-top: 42px;
}

hr {
  width: 70%;
}

.links-social {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding: 5px;
}

.links-social a:hover {
  color: red;
}

a i {
  margin-left: 10px;
}
.btn:active{
  color: lavender;
}

.btn:hover:active {
  color: bisque !important;
}
.active {
  color: rgb(0, 0, 0);
}

.navbar{
  background-color: darkgrey;
}

.about-me-details{
  margin-top:-40%;
  float: left;
}

.about-me-details p{
  clear: both;
  float: right;
  width: 50%;
  font-size: 18px;
  display: block;
}


.card-group{
 width: 70%;
}
.card-img-top{
  width: 100%;
}
.card{
  width:20%;
}

.card-deck{
  border: 4px solid gray;
  border-radius: 15px;
}

.footer{
  text-align: center;
  margin-top: 10px;
}
